<template>
    <ul v-if="type == 'list'" id='FolderFullTree' class='filetree treeview py-1'>
        <li class='expandable'>
            <div :class="{'hitarea collapsable-hitarea':organizationSpace.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' id="Organization" @click="GetFolderList('', '', 1, 'Organization Space','Organization')"
                    href='javascript:;'>Organization Space</a>
            </span>
            <ul v-if="organizationSpace.length>0" id='FolderFullTree' class='filetree treeview' style="display: none;">
                <ChildFolder v-if="organizationSpace" :routeClass="routeClass" :organizationSpace="organizationSpace" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu">
                </ChildFolder>
            </ul>
        </li>
        <li class='expandable' style="display: block;">
            <div  :class="{'hitarea collapsable-hitarea':RecentFolders.length>0}"></div>
            <span class='folder'>
                <a id="Recent" class='folderlink' @click="GetFolderList('', '', '', 'Recently Added','Recent')" href='javascript:;'>Recently
                    Added</a>
            </span>
            <ul v-if="RecentFolders.length>0" id='FolderFullTree' class='filetree treeview' style="display: none;">
                <ChildFolder v-if="RecentFolders" :routeClass="routeClass" @FolderIdMoveLeftMenu=FolderIdMoveLeftMenu :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :RecentFolders="RecentFolders"></ChildFolder>
            </ul>
        </li>
        <li class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':pinnedFolders.length>0}"></div>
            <span class='folder'>
                <a id="Pinned" class='folderlink' @click="GetFolderList('', '', '', 'Pinned Folders','Pinned')" href='javascript:;'>Pinned
                    Folders</a>
            </span>
            <ul v-if="pinnedFolders.length>0" class='filetree treeview' style="display: none;">
                <ChildFolder v-if="pinnedFolders" :routeClass="routeClass" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :pinnedFolders="pinnedFolders"></ChildFolder>
            </ul>
        </li>
        <li :class="{'collapsable':addClsLeftMenu==true,'expandable':addClsLeftMenu!=true}" class="pt-1" style="display: block;">
            <div  :class="{ 'hitarea collapsable-hitarea':  MySpaceFolders.length > 0}"></div>
            <span class='folder'>
                <a class='folderlink' id="MySpace"  @click="GetFolderList('', '', '', 'My Space','MySpace')">My Space</a>
            </span>
            <ul v-if="MySpaceFolders.length>0 && addClsLeftMenu!=true" style="display: none;">
            <ChildFolder v-if="MySpaceFolders" :AdditionalDriveId="AdditionalDriveId" :routeClass="routeClass" :checkActionType="checkActionType" :type="type" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                    @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderTree="MySpaceFolders" :addClsLeftMenu="addClsLeftMenu">
                </ChildFolder>
            </ul>
            <ul v-if="MySpaceFolders.length>0 && addClsLeftMenu==true" style="display: block;">
            <ChildFolder v-if="MySpaceFolders" :routeClass="routeClass" :AdditionalDriveId="AdditionalDriveId" :checkActionType="checkActionType" :type="type" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                    @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderTree="MySpaceFolders" :addClsLeftMenu="addClsLeftMenu">
                </ChildFolder>
            </ul>
        </li>
        <li class="expandable" style="display: block;">
            <div  :class="{'hitarea collapsable-hitarea':sharedFolders.length>0}"></div>
            <span class='folder'>
                <a id="Shared" class='folderlink' @click="GetFolderList('', '', '', 'Shared Folders','Shared')" href='javascript:;'>Shared Folder</a>
            </span>
            <ul v-if="sharedFolders.length>0" class='filetree treeview' style="display: none;">
                <ChildFolder v-if="sharedFolders" :sharedChildFolder="sharedFolders"
                    @SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"  :SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                     
                    :userids="userid">
                </ChildFolder>
            </ul>
        </li>

    </ul>
    <ul v-else-if="type == 'Additional'" :id='treeId' class='filetree treeview'>
        <li v-for="item in dynamicLeftMenu"  :class="{'collapsable':addClsLeftMenu==true,'expandable':addClsLeftMenu!=true}" class="pt-1" style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':organizationSpace.length>0}"></div>
            <span class='folder' >
                <a id="AdditionalStorage" v-bind:AdditionalFolderid="item.additional_storage_folder_id" v-bind:AdditionalDriveId="AdditionalDriveId"  class='folderlink' @click="GetFolderList('', '', 1, 'Organization Space','AdditionalStorage',AdditionalDriveId,item.additional_storage_folder_id)"
                    href='javascript:;'>{{item.FOLDER_NAME}}</a>
            </span>
            <ul v-if="organizationSpace.length>0 && addClsLeftMenu!=true" id='FolderFullTree3' class='filetree treeview' style="display: none;">
                <ChildFolder v-if="organizationSpace" :checkActionType="checkActionType" :AdditionalDriveId="AdditionalDriveId" :organizationSpace="organizationSpace" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu">
                </ChildFolder>
            </ul>
            <ul v-if="organizationSpace.length>0 && addClsLeftMenu==true" id='FolderFullTree3' class='filetree treeview' style="display: block;">
                <ChildFolder v-if="organizationSpace" :checkActionType="checkActionType" :AdditionalDriveId="AdditionalDriveId" :organizationSpace="organizationSpace" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu">
                </ChildFolder>
            </ul>
        </li>
        <li class='expandable' style="display: block;">
            <div  :class="{'hitarea collapsable-hitarea':RecentFolders.length>0}"></div>
            <span class='folder'>
                <a id="Recent" class='folderlink' v-bind:AdditionalDriveId="AdditionalDriveId" @click="GetFolderList('', '', '', 'Recently Added','Recent',AdditionalDriveId)" href='javascript:;'>Recently
                    Added</a>
            </span>
            <ul v-if="RecentFolders.length>0" id='FolderFullTree3'  class='filetree treeview' style="display: none;">
                <ChildFolder v-if="RecentFolders" :checkActionType="checkActionType" :AdditionalDriveId="AdditionalDriveId" :routeClass="routeClass" @FolderIdMoveLeftMenu=FolderIdMoveLeftMenu :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :RecentFolders="RecentFolders"></ChildFolder>
            </ul>
        </li>
        <li :class="{'collapsable':addClsLeftMenu==true,'expandable':addClsLeftMenu!=true}" class="pt-1" style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':pinnedFolders.length>0}"></div>
            <span class='folder'>
                <a id="Pinned" class='folderlink' v-bind:AdditionalDriveId="AdditionalDriveId" @click="GetFolderList('', '', '', 'Pinned Folders','Pinned',AdditionalDriveId)" href='javascript:;'>Pinned
                    Folders</a>
            </span>
            <ul v-if="pinnedFolders.length>0" class='filetree treeview' id='FolderFullTree3' style="display: none;">
                <ChildFolder v-if="pinnedFolders" :checkActionType="checkActionType" :AdditionalDriveId="AdditionalDriveId" :routeClass="routeClass" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :pinnedFolders="pinnedFolders"></ChildFolder>
            </ul>
        </li>
        <li :class="{'collapsable':addClsLeftMenu==true,'expandable':addClsLeftMenu!=true}" class="pt-1" style="display: block;">
            <div  :class="{ 'hitarea collapsable-hitarea':  MySpaceFolders.length > 0}"></div>
            <span class='folder'>
                <a class='folderlink' id="MySpace" v-bind:AdditionalDriveId="AdditionalDriveId"  @click="GetFolderList('', '', '', 'My Space','AdditionalStorage',AdditionalDriveId)">{{ $t('MySpace') }}</a>
            </span>
            <ul v-if="MySpaceFolders.length>0 && addClsLeftMenu!=true" id='FolderFullTree3' style="display: none;">
            <ChildFolder v-if="MySpaceFolders" :AdditionalDriveId="AdditionalDriveId" :routeClass="routeClass" :checkActionType="checkActionType" :type="type" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                    @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderTree="MySpaceFolders" :addClsLeftMenu="addClsLeftMenu">
                </ChildFolder>
            </ul>
            <ul v-if="MySpaceFolders.length>0 && addClsLeftMenu==true" id='FolderFullTree3' style="display: block;">
            <ChildFolder v-if="MySpaceFolders" :routeClass="routeClass" :AdditionalDriveId="AdditionalDriveId" :checkActionType="checkActionType" :type="type" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                    @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderTree="MySpaceFolders" :addClsLeftMenu="addClsLeftMenu">
                </ChildFolder>
            </ul>
        </li>
        <li class="expandable" style="display: block;">
            <div  :class="{'hitarea collapsable-hitarea':sharedFolders.length>0}"></div>
            <span class='folder'>
                <a id="Shared" class='folderlink' @click="GetFolderList('', '', '', 'Shared Folders','Shared',AdditionalDriveId)" href='javascript:;'>Shared Folder</a>
            </span>
            <ul v-if="sharedFolders.length>0" class='filetree treeview' style="display: none;">
                <ChildFolder v-if="sharedFolders" :sharedChildFolder="sharedFolders"
                    @SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"  :SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                     
                    :userids="userid">
                </ChildFolder>
            </ul>
        </li>
    </ul>
    <ul v-else-if="type != 'Additionals' && type != 'List'" id='FolderFullTree1' class='filetree treeview'>
        <li v-if="mySpaceOrOrganization==1" class='expandable'>
            <div :class="{'hitarea collapsable-hitarea':organizationSpace.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', 1, 'Organization Space','Organization')"
                    href='javascript:;'>Organization Space</a>
            </span>
            <ul v-if="organizationSpace.length>0" id='FolderFullTree' class='filetree treeview' style="display: none;">
                <ChildFolder v-if="organizationSpace" :checkActionType="checkActionType" :organizationSpace="organizationSpace" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu">
                </ChildFolder>
            </ul>
        </li>
    </ul>
    <ul v-else-if="type != 'list'" id='FolderFullTree1' class='filetree treeview'>
        <li v-if="mySpaceOrOrganization==1" class='expandable'>
            <div :class="{'hitarea collapsable-hitarea':organizationSpace.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', 1, 'Organization Space','Organization')"
                    href='javascript:;'>Organization Space</a>
            </span>
            <ul v-if="organizationSpace.length>0" id='FolderFullTree' class='filetree treeview' style="display: none;">
                <ChildFolder v-if="organizationSpace" :checkActionType="checkActionType" :organizationSpace="organizationSpace" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu">
                </ChildFolder>
            </ul>
        </li>
        <li  v-if="type=='list'" class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':organizationSpace.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', '', 'Recently Added','Recent')" href='javascript:;'>Recently
                    Added</a>
            </span>
            <ul class='filetree treeview'  style="display: none;">
                <ChildFolder v-if="RecentFolders" @FolderIdMoveLeftMenu=FolderIdMoveLeftMenu :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :RecentFolders="RecentFolders"></ChildFolder>
            </ul>
        </li>
        <li  v-if="type=='list'" class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':pinnedFolders.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', '', 'Pinned Folders','Pinned')" href='javascript:;'>Pinned
                    Folders</a>
            </span>
            <ul class='filetree treeview' style="display: none;">
                <ChildFolder :type="type" v-if="pinnedFolders" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                     :pinnedFolders="pinnedFolders"></ChildFolder>
            </ul>
        </li>
        <li v-if="mySpaceOrOrganization!=1" class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':MySpaceFolders.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', '', 'My Space','MySpace')">My Space</a>
            </span>
            <ul v-if="MySpaceFolders.length>0" style="display: none;">
                <ChildFolder :checkActionType="checkActionType" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu 
                    @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderTree="MySpaceFolders" :addClsLeftMenu="addClsLeftMenu">
                </ChildFolder>
            </ul>
        </li>
        <li  v-if="type=='list'" class='expandable' style="display: block;">
            <div :class="{'hitarea collapsable-hitarea':sharedFolders.length>0}"></div>
            <span class='folder'>
                <a class='folderlink' @click="GetFolderList('', '', '', 'Shared')" href='javascript:;'>Shared Folder</a>
            </span>
            <ul class='filetree treeview' style="display: none;">
                <ChildFolder v-if="sharedFolders" :sharedChildFolder="sharedFolders" :FolderIdMoveLeftMenu=FolderIdMoveLeftMenu
                    @SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                    :SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu" 
                    
                    :userids="userid">
                </ChildFolder>
            </ul>
        </li>

    </ul>
</template>
<script>
import ChildFolder from './ChildFolder.vue';
export default {
    components: { ChildFolder },
    props: {
        type: null,
        organizationSpace: null,
        RecentFolders: null,
        pinnedFolders: null,
        MySpaceFolders: null,
        treeId:null,
        sharedFolders: {
            type: Array,
            required: true
        },
        dynamicLeftMenu:{
            type: Array,
            required: false
        },
        type:null,
        mySpaceOrOrganization:{
            type:Number,
            required: false
        },
        checkActionType:{
            type:String,
            required: false
        },
        routeClass:{
            type:String,
            required: false
        },
        refCode:{
            type:String,
            required: false
        },
        refCodes:{
            type:String,
            required: false
        },
        addClsLeftMenu:{
            type:Boolean,
            required: false
        },
        treeOpenCode:{
            type:String,
            required: false
        },
        userid: {
            type: Number,
            required: true
        },
        AdditionalDriveId:{
            type:String,
            required: false
        }
    },
    data() {
        return {
            selectedFolderClass:"selectedfolder"
        };
    },
    
    created: function(){
       var pathname = window.location.pathname;
       if(pathname.split("/")[1] == 'RecyleBin' || pathname.split("/")[1] == 'File' ||  pathname.split("/")[1]=='ViewFolderDetails' || pathname.split("/")[1]=='ViewFileDetails')
       {
        this.selectedFolderClass=""
       }
    },
    mounted: function () {
        var id='#'+this.treeId
        $(id).treeview();
        $("body").on("click", ""+id+" .folderlink", function () {
            $(""+id+" .folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
        if(this.refCode=="MySpace"){
            $("#MySpace").addClass("selectedfolder");
        }
        else if(this.refCode=="Organization"){
            $("#Organization").addClass("selectedfolder");
        }
        else if(this.refCode=="Recent"){
            $("#Recent").addClass("selectedfolder");
        }
        else if(this.refCode=="Pinned"){
            $("#Pinned").addClass("selectedfolder");
        }
        else if(this.refCode=="Shared"){
            $("#Shared").addClass("selectedfolder");
        }
        else if(this.refCode=="AdditionalStorage"){
            $("#AdditionalStorage").addClass("selectedfolder");
        }
        if (this.type == 'list') {
            $("#FolderFullTree").treeview();
            $("body").on("click", "#FolderFullTree .folderlink", function () {
                $("#FolderFullTree .folderlink").removeClass("selectedfolder");
                $(this).addClass("selectedfolder");
            });
        }
    },
    methods: {
        FolderIdMoveLeftMenu: function (data, parentid, actionVal, name,routeListingcode,additionalStorageFolderId,driveid) {
            
            this.$emit('FolderIdMoveLeftMenu',data, parentid, actionVal, name,routeListingcode,additionalStorageFolderId,driveid);
        },
        StorageDocumentListing:function(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode, sharedPermission,pageno,clikType,addFolderId,driveId){
            //debugger
            var vm=this;
            vm.$emit('StorageDocumentListing',folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode, sharedPermission,pageno,clikType,addFolderId,driveId)
        },
        GetFolderList: function (data, parentid, actionVal, name,routeListingcode,driveid,additionalFolderId) {
            debugger
            if(routeListingcode!='AdditionalStorage'){
                $("#AdditionalStorage").removeClass("selectedfolder");
                $("#MySpace").removeClass("selectedfolder");
                $("#Recent").removeClass("selectedfolder");
                $("#Shared").removeClass("selectedfolder");
                $("#Organization").removeClass("selectedfolder");
            }else{
                $("#MySpace").removeClass("selectedfolder");
                $("#Recent").removeClass("selectedfolder");
                $("#Shared").removeClass("selectedfolder");
                $("#Organization").removeClass("selectedfolder");
                $("#Pinned").removeClass("selectedfolder");
            }
            var selectedfolder=$('.selectedfolder').attr('id');
            try {
                if(routeListingcode!=selectedfolder && selectedfolder!=undefined){
                this.$parent.$parent.$refs.listing.searchCondition ="";
                this.$parent.$parent.$refs.listing.listType='ListView';

            }
            } catch (error) {
                console.log(error)
            }
           
            this.$emit('Folderlisting',data, parentid, actionVal, name,routeListingcode,driveid,additionalFolderId);
        },
        SharedFolderIdMoveLeftMenu: function (data, parentid, actionVal, name, permission,routeListingcode) {
            this.$emit('SharedFolderIdMoveLeftMenu',data, parentid, actionVal, name,permission,routeListingcode);
        }
    },

}
</script>