<template>
    
    <section id="main-content" class="main-content">
        <loader :is-visible="isLoading"></loader>
        <!-- start page heading -->
        <section class="main-content">
            <div class="">
                <!-- start left panel -->
                <div class="left-menu-tab" id="leftmenutree">
                    <div class="col-md-12 p-0">
                        <div class="theme-primary partition">
                            <span class="p-name text-white">Document Storage</span>
                        </div>
                    </div>
                    <div class="accordion md-accordion left-filter mt-2" id="accordionEx" role="tablist"
                        aria-multiselectable="true">
                        <div class="card">
                            <div class="card-header filter-head" role="tab" id="headingOne1">
                                <h5 class="mb-0">
                                    <em class="fa fa-file font-16"></em> Files
                                </h5>
                            </div>
                        </div>
                    </div>
                    <div id="main" class="collapse show border FullTreeData"
                        style="max-height:90vh; overflow-y: auto;">
                        <DocCommonTreeView :organizationSpace="organizationSpace"
                        :RecentFolders="RecentPinnedFolderArray"
                        :pinnedFolders="pinnedFolderArray"
                        :MySpaceFolders="FolderArray"
                        :sharedFolders="sharedChildFolder"
                        :routeClass="routeClass"
                        :AdditionalDriveId="AdditionalDriveId"
                        :refCode="refCode"
                        :addClsLeftMenu="addClsLeftMenu"
                        type="list"
                        @Folderlisting="Folderlisting"
                        @SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                        :SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                        @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu"
                        :checkActionType="checkActionType" :userid="userid" :treeOpenCode="treeOpenCode"
                         ></DocCommonTreeView>                        
                    </div>
                    <a id="aShareFolder" class="btn btn-info w-100" title="" data-toggle-tooltip="tooltip" data-original-title="Share" @click="GoToListing()">
                        <em class="fa fa-recycle text-white" style="font-size: 18px;"></em> {{$t('RecycleBin')}}
                    </a>
                    <AdditionalStorageLeftMenu  :organizationSpace="additionalOrganizationSpace"
                        :RecentPinnedFolderArray="RecentPinnedFolderArray"
                        :pinnedFolderArray="pinnedFolderArray"
                        :FolderArray="FolderArray"
                        :sharedChildFolder="sharedChildFolder"
                        :routeClass="routeClass"
                        :folderTreeList="folderTreeList"
                        refCodes="AdditionalStorage"
                        :addClsLeftMenu="addClsLeftMenu"
                        @Folderlisting="Folderlisting"
                        @SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                        :SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                        @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu"
                        :checkActionType="checkActionType" :userid="userid" :treeOpenCode="treeOpenCode"
                        ></AdditionalStorageLeftMenu>
                </div>
            </div>
        </section>
        
    </section>
    
</template>
<script>
import ChildFolder from "./ChildFolder.vue"
import AdditionalStorageLeftMenu from "./Common/AdditionalStorageLeftMenu.vue";
import DocCommonTreeView from "./DocCommonTreeView.vue";
export default {
    components: {
        ChildFolder,
        DocCommonTreeView,
        AdditionalStorageLeftMenu
    },
    updated:function(){
        $("#FolderFullTree3").treeview();
    },
    props: {
        treeOpenCode:{
            type: String,
            required: true
        },
        additionalOrganizationSpace:{
            type: Array,
            required: false
        },
        FolderArray: {
            type: Array,
            required: true
        },
        SharedFolder: {
            type: Array,
            required: true
        },
        userid: {
            type: Number,
            required: true
        },
        sharedChildFolder: {
            type: Array,
            required: true
        },
        RecentPinnedFolderArray: {
            type: Array,
            required: true
        },
        pinnedFolderArray: {
            type: Array,
            required: true
        },
        organizationSpace:{
            type: Array,
            required: true 
        },
        addClsLeftMenu:{
            type: Boolean,
            required: false 
        },
        folderTreeList:{
            type: Array,
            required: true 
        },
        // FolderIdMoveLeftMenu:{
        //     type: Function,
        //     required: true
        // },
        // SharedFolderIdMoveLeftMenu:{
        //     type: Function,
        //     required: true
        // },
        // OrganizationFolderIdMoveLeftMenu:{
        //     type: Function,
        //     required: true
        // },
        // FindalFolderArray:{
        //     type: Array,
        //     required: true
        // },
        checkActionType:{
            type: String,
            required: false
        },
        routeClass:null,
        refCode:{
            type: String,
            required: false
        },
        
    },
    data() {
        return {
            isLoading:false,
            // FilesListingData: [],
            FolderIds: null,
        }

    },
    
   
    methods: {
        FolderIdMoveLeftMenu(data, parentid,actionVal,name,aid,additionalStorageFolderId,driveid) {
            var vm = this;
            vm.FolderIds = data;
            if(name=="My Space"){
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                vm.$parent.StorageDocumentListing(data, parentid,actionVal,name,'','','','MySpace','','','Tree',additionalStorageFolderId,driveid);
                var code=driveid!=null && driveid!=""?"AdditionalStorage":"MySpace";
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: data ,ParentName:name,RefCode:code,AddClass:aid,AdditionalStorage:additionalStorageFolderId==null?'':additionalStorageFolderId,AdditionalStorageId:driveid} });
            }
            else if(name=="Recently Added"){
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                vm.$parent.StorageDocumentListing(data, parentid,actionVal,name,'','','','Recent','','','Tree'); 
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: data ,ParentName:name,RefCode:'Recent',AddClass:aid} });
            }
            else if(name=="Pinned Folders"){
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                vm.$parent.StorageDocumentListing(data, parentid,actionVal,name,'','','','Pinned','','','Tree'); 
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: data ,ParentName:name,RefCode:'Pinned',AddClass:aid} });
            }
            else if(name=="Shared"){
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                vm.$parent.StorageDocumentListing(data, parentid,actionVal,name,'','','','Shared','','','Tree'); 
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: data ,ParentName:name,RefCode:'Shared',AddClass:aid} });
            }
            else if(name=="Organization Space"){
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                vm.$parent.StorageDocumentListing(data, parentid,actionVal,name,'','','','Organization','','','Tree',additionalStorageFolderId,driveid);
                var code=driveid!=null && driveid!=""?"AdditionalStorage":"Organization";
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: data ,ParentName:name,RefCode:code,AddClass:aid,AdditionalStorage:additionalStorageFolderId==null?'':additionalStorageFolderId,AdditionalStorageId:driveid} }); 
            }
        },
        SharedFolderIdMoveLeftMenu(data, parentid,actionVal,name,permission) {
            var vm = this;
            vm.FolderIds = data;
            vm.$parent.StorageDocumentListing(data, parentid,actionVal,name,'','','','Shared',permission);
            this.$router.push({ path: "/File/DashBoard", query: { FolderId: data,ParentName:name ,RefCode:'Shared',Permission:permission} });
        },
        Folderlisting: function (data, parentid,actionVal,name,routeListingcode,driveid,additionalFolderId) {
            //debugger
            var vm = this;
            var pathname = window.location.pathname;
            if(pathname.split("/")[1] == 'RecyleBin' || pathname.split("/")[1] == 'ViewFileDetails' ||  pathname.split("/")[1] == 'ViewFolderDetails')
            {
                vm.$router.push({ name: 'DashBoard',query: { FolderId:0,RefCode:routeListingcode,ParentName:name,AdditionalStorage:null,AdditionalStorageId:driveid}  });
            }
            else if(name=="My Space"  && routeListingcode!="AdditionalStorage")
            {
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                 vm.$parent.PagerData(1, 'My Space','','',10,data);
                 this.$router.push({ path: "/File/DashBoard", query: { FolderId: 0,ParentName:name ,RefCode:'MySpace'} });
            }
            else if((name=="Organization Space" || pathname.split("/")[1] == 'RecyleBin') && routeListingcode!="AdditionalStorage")
            {
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                 vm.$parent.PagerData(1, 'Organization Space','','',10,data);
                 this.$router.push({ path: "/File/DashBoard", query: { FolderId: 0 ,ParentName:name,RefCode:'Organization'} });
            }
            else if(name=="Pinned Folders")
            {
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                vm.$parent.PagerData(1, 'Pinned Folders','','',10,data,'',driveid);
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: 0,ParentName:name, RefCode:'Pinned'} });
            }
            else if(name=="Recently Added"){
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                 vm.$parent.PagerData(1, 'Recently Added','','',10,data,'',driveid);
                 this.$router.push({ path: "/File/DashBoard", query: { FolderId: 0 ,ParentName:name,RefCode:'Recent'} });
            }
            else if(name=="Shared Folders"){
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                 vm.$parent.PagerData(1, 'Shared Folders','','',10,data,'',driveid);
                 this.$router.push({ path: "/File/DashBoard", query: { FolderId: 0 ,ParentName:name, RefCode:'Shared'} });
            }
            else if(routeListingcode=="AdditionalStorage"){
                if(name=="Organization Space"){
                    name="Organization"
                }else if(name=="My Space"){
                    name="MySpace"
                }
                 // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)
                 vm.$parent.PagerData(1, 'AdditionalStorage','','',10,data,'',driveid,name,additionalFolderId);
                 this.$router.push({ path: "/File/DashBoard", query: { FolderId: 0 ,ParentName:name, RefCode:'AdditionalStorage',AdditionalStorageId:driveid} });
            }
            
            vm.$emit("BreadCrumb",name);
        },
        GoToListing: function () {
            var vm = this;
            vm.isLoading=true;
            vm.$router.push({ name: 'RecyleBin' });
            vm.isLoading=false;
        }
    },

}
</script>